import React from 'react';
import ProgressBar from 'ps-react-abhiisheek/ProgressBar';

/** 100% Progress with Height 20 */
const Example100Percent20Height = () => {
  return <ProgressBar percent={100} width={150} height={20} />;
};

export default Example100Percent20Height;



